*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}
.contain{
  width: 100vw;
  height:100vh;
  position:relative;
  display: grid;
  place-items:center;
}


.cards{
  width:50vmin;
  height:90%;
  box-shadow:0px 0px 10px black,
  10px 20px 20px black;  ;
  position:absolute;
  overflow-x:hidden;
  overflow-y:scroll;
  border-radius:1vmin;
  scroll-snap-type:both mandatory;
  scroll-snap-align:start;

}
.card{
  width:100%;
  height:100%;
  position:relative;

  margin-bottom:5px;
  scroll-snap-type:both mandatory;
  scroll-snap-align:start;
  transition:all 400ms linear;
}
.card >img{
  width:100%;
  height:55%;
  position:absolute;
}
.head{
  font-size:2vmin;
  width: 100%;
  position:absolute;
  top:53%;;
 padding:4vmin;
 font-weight:bold;
 text-decoration:underline;

}
.info{
  position:absolute;
  top:65%;
  padding:3vmin;
  height:25%;
  overflow: scroll;
  overflow: hidden;
  text-overflow: ellipsis;
}
.price{
  position:absolute;
  bottom:2%;
  left:5%;
  background:rgb(74, 255, 113);
  color:rgb(0, 0, 0);
  width:15vmin;
  height:5vmin;
  display:grid;
  place-items:center;
  font-weight:bold;
}
#notInterested{
  position:absolute;
  bottom:2.5%;
  right:5%;
  width:15vmin;
  height:5vmin;
  border:none;
  background:rgb(255, 51, 51);
  color:white;
}

.remove{
  opacity: 0;
  transform:translateY(-50%);
}

@media only screen and (max-width:550px){
  .cards{
    width:70%;
  }
  #notInterested{
    width: 20vmin;
  }
  .head{
    font-size:4vmin;
  }
}